export default {
  name: 'imglink',
  props: {
  	img: {
  		type: Object,
  		default : ()=>{
  			return {
  				src: 'http://i8.mifile.cn/v1/a1/1d338200-1be1-f868-9695-9d5ae0d6c2c6.webp?width=720&heihgt=440',
  				link: '/shopping',
  				maxheight: true,
  				maxwidth: true
  			}
  		}
  	}
  },
  data () {
    return {
      classActive: {
      	mw100: this.img.maxwidth,
      	mh100: this.img.maxheight
      }
    }
  },
  methods: {
  	go(){
  		this.$router.push(this.img.link)
  	}
  }
}



