
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
  components: {
  	Tabbar,
  	TabbarItem
  },
  data(){
  	return{
  		index: 0
  	}
  },
  name: 'app',
  watch: {
  	'$route': 'select'
  },
  methods:{
	select(){
		switch (this.$route.path) {
			case '/':
				this.index = 0
				break;
			case '/classic':
				this.index = 1
				break;
			case '/cart':
				this.index = 2
				break;
			case '/me':
				this.index = 3
				break;
			default:
				// statements_def
				break;
		}
	}
  }
}



