import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/home';
import Shopping from '@/components/shopping';
import Cart from '@/components/cart'
import Classic from '@/components/classic'
import Me from '@/components/me'
import Artice from '@/components/artice'

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: '/M/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/shopping',
      name: 'shopping',
      components: {
      	shopping:Shopping
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/classic',
      name: 'classic',
      component: Classic
    },
    {
      path: '/me',
      name: 'me',
      component: Me
    },
    {
      path: '/artice',
      name: 'artice',
      components: {
        artice: Artice
      }
    }
  ]
})



