
import XImg from 'vux/src/components/x-img/index.vue'

export default {
  name: 'card',
  components: {
    XImg
  },
  props: {
    product: {
      type: Object,
      default: ()=>{
        return {
          src:'http://i8.mifile.cn/v1/a1/ba9e445e-fe50-d697-1245-a5cde1d15126.webp?width=360&height=360',
          name: '5 64GB',
          info: '',
          price: 999,
          oldprice:'555',
          linkName: 'shopping',
          discount: true
        }
      }
    }
  },
  data () {
    return {
      
    }
  },
  methods:{
    go(){
      let name = this.product.linkName;
      this.$router.push({ name, params: this.product})
    }
  }
}



